'use client'

import { addFavorite } from '@/actions/user.action'
import useAction from '@/hooks/use-action'
import { toast } from '@/hooks/use-toast'
import { formatPrice } from '@/lib/utils'
import { IProduct } from '@/types'
import { Environment, Float, PresentationControls } from '@react-three/drei'
import { Canvas, useFrame } from '@react-three/fiber'
import { Heart } from 'lucide-react'
import { useRouter } from 'next/navigation'
import { FC, MouseEvent, Suspense, useRef, useState } from 'react'
import NoSSR from 'react-no-ssr'
import * as THREE from 'three'
import { Button } from '../ui/button'

interface Props {
	product: IProduct
}

const ProductModel = ({ image }: { image: string }) => {
	const meshRef = useRef<THREE.Mesh>(null)

	useFrame(state => {
		if (!meshRef.current) return
		meshRef.current.rotation.y = state.clock.getElapsedTime() * 0.3
	})

	return (
		<mesh ref={meshRef} castShadow receiveShadow>
			<boxGeometry args={[1.5, 1.5, 1.5]} />
			<meshStandardMaterial>
				<primitive
					attach='map'
					object={new THREE.TextureLoader().load(image)}
				/>
			</meshStandardMaterial>
		</mesh>
	)
}

const ProductCard: FC<Props> = ({ product }) => {
	const { isLoading, onError, setIsLoading } = useAction()
	const router = useRouter()
	const [hovered, setHovered] = useState(false)

	const onFavourite = async (e: MouseEvent) => {
		e.stopPropagation()
		setIsLoading(true)
		const res = await addFavorite({ id: product._id })
		if (res?.serverError || res?.validationErrors || !res?.data) {
			return onError('Something went wrong')
		}
		if (res.data.failure) {
			return onError(res.data.failure)
		}
		if (res.data.status === 200) {
			toast({ description: 'Added to favorites' })
			setIsLoading(false)
		}
	}

	return (
		<div
			onClick={() => router.push(`/product/${product._id}`)}
			className='cursor-pointer relative'
			onMouseEnter={() => setHovered(true)}
			onMouseLeave={() => setHovered(false)}
		>
			<div className='bg-secondary relative group h-[250px] overflow-hidden'>
				<NoSSR>
					<Canvas
						shadows
						dpr={[1, 2]}
						camera={{ position: [0, 0, 4], fov: 50 }}
					>
						<Suspense fallback={null}>
							<PresentationControls
								global
								rotation={[0, 0, 0]}
								polar={[-0.1, 0.1]}
								azimuth={[-0.1, 0.1]}
								config={{ mass: 2, tension: 400 }}
								snap={{ mass: 4, tension: 400 }}
							>
								<Float rotationIntensity={hovered ? 0.4 : 0.1}>
									<ProductModel image={product.image!} />
								</Float>
							</PresentationControls>
							<Environment preset='city' />
						</Suspense>
					</Canvas>
				</NoSSR>
				<div className='absolute right-0 top-0 z-40 opacity-0 group-hover:opacity-100 transition-all'>
					<Button size={'icon'} disabled={isLoading} onClick={onFavourite}>
						<Heart />
					</Button>
				</div>
			</div>
			<div className='flex justify-between items-center mt-2 text-sm'>
				<h1 className='font-bold line-clamp-1'>{product.title}</h1>
				<NoSSR>
					<p className='font-medium'>{formatPrice(product.price!)}</p>
				</NoSSR>
			</div>
			<p className='text-xs text-muted-foreground'>{product.category}</p>
		</div>
	)
}

export default ProductCard
